/**
* Line for under a node with children
*/
.rst__lineChildren {
height: 100%;
display: inline-block;
position: absolute;
}
.rst__lineChildren::after {
content: '';
position: absolute;
background-color: #35414B;
width: 1px;
left: 50%;
bottom: 0;
height: 10px;
}

.rst__rtl.rst__lineChildren::after {
right: 50%;
left: initial;
}